import { cva } from 'cva'
import type { ClassRecord } from '@/shared/lib/style'
import type { Size, Variant } from './types'

export const spinner = cva({
  base: 'block animate-spin rounded-full !border-r-transparent',
  variants: {
    variant: {
      primary: 'border-indigo-600',
      secondary: 'border-gray-300',
    } satisfies ClassRecord<Variant>,
    size: {
      xs: 'size-5 border-2',
      sm: 'size-6 border-2',
      base: 'size-7 border-[3px]',
      lg: 'size-8 border-4',
      xl: 'size-9 border-4',
    } satisfies ClassRecord<Size>,
  },
  defaultVariants: {
    variant: 'primary',
    size: 'base',
  },
})
